.search {
  width: 100%;
  position: relative;
}

.searchTerm {
  float: left;
  width: 100%;
  border: 3px solid #182628;
  padding: 5px;
  height: 38px;
  border-radius: 5px;
  outline: none;
  color: #9DBFAF;
}

.searchTerm:focus{
  color: #05386B;
}

.searchButton {
  position: absolute;  
  right: -50px;
  width: 40px;
  height: 38px;
  border: 1px solid #182628;
  background: #182628;
  text-align: center;
  color: #fff;
  border-radius: 5px;
  cursor: pointer;
  font-size: 20px;
}

/*Resize the wrap to see the search bar change!*/
.wrap{
  width: 35%;
  position: absolute;
  top: 5.5%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
